"use client"

import { useEffect } from "react"

export function useSmoothScroll() {
  useEffect(() => {
    let velocity = 0
    let targetScrollY = window.scrollY
    let currentScrollY = window.scrollY
    const friction = 0.95
    const acceleration = 0.05

    // Handle mouse wheel
    const handleWheel = (e: WheelEvent) => {
      e.preventDefault()
      targetScrollY += e.deltaY
      targetScrollY = Math.max(0, Math.min(targetScrollY, document.documentElement.scrollHeight - window.innerHeight))
    }

    // Handle touch scroll
    let touchStartY = 0
    const handleTouchStart = (e: TouchEvent) => {
      touchStartY = e.touches[0].clientY
    }

    const handleTouchMove = (e: TouchEvent) => {
      const touchY = e.touches[0].clientY
      const delta = touchStartY - touchY
      targetScrollY += delta * 0.5
      targetScrollY = Math.max(0, Math.min(targetScrollY, document.documentElement.scrollHeight - window.innerHeight))
      touchStartY = touchY
    }

    // Smooth scroll animation loop
    const animateScroll = () => {
      velocity = (targetScrollY - currentScrollY) * acceleration
      velocity *= friction
      currentScrollY += velocity

      if (Math.abs(velocity) > 0.1) {
        window.scrollTo(0, currentScrollY)
        requestAnimationFrame(animateScroll)
      } else {
        currentScrollY = Math.round(currentScrollY)
        window.scrollTo(0, currentScrollY)
      }
    }

    window.addEventListener("wheel", handleWheel, { passive: false })
    window.addEventListener("touchstart", handleTouchStart, { passive: true })
    window.addEventListener("touchmove", handleTouchMove, { passive: true })

    // Start animation loop
    const animationFrameId = requestAnimationFrame(animateScroll)

    // Update target on manual scroll
    const handleScroll = () => {
      targetScrollY = window.scrollY
      currentScrollY = window.scrollY
    }

    window.addEventListener("scroll", handleScroll, { passive: true })

    return () => {
      window.removeEventListener("wheel", handleWheel)
      window.removeEventListener("touchstart", handleTouchStart)
      window.removeEventListener("touchmove", handleTouchMove)
      window.removeEventListener("scroll", handleScroll)
      cancelAnimationFrame(animationFrameId)
    }
  }, [])
}
